import React from "react";

function OurServices(props) {
  return (
    <React.Fragment>
      <h4 className="headTitle">{props.headtitle}</h4>

      <div className="cards">
        <div className="card1">
          <img src={props.img1} className="thumbnail" />
          <div className="text">
            <h5 className="title">{props.title1}</h5>
            <ul>
              <li>{props.sub1}</li>
              <li>{props.sub2}</li>
              <li>{props.sub3}</li>
            </ul>
          </div>
        </div>
        <div className="card1">
          <img src={props.img2} className="thumbnail" />
          <div className="text">
            <h5>{props.title2}</h5>
            <ul>
              <li>{props.sub4}</li>
              <li>{props.sub5}</li>
              <li>{props.sub6}</li>
            </ul>
          </div>
        </div>
        <div className="card1">
          <img src={props.img3} className="thumbnail" />
          <div className="text">
            <h5>{props.title3}</h5>
            <ul>
              <li>{props.sub7}</li>
              <li>{props.sub8}</li>
              <li>{props.sub9}</li>
            </ul>
          </div>
        </div>
      </div>
      <div className="overallbutton">
        <a className="btn-default color-secondary size-sm" href="#button">
          View More
        </a>
      </div>
    </React.Fragment>
  );
}

export default OurServices;
